<script>
  export let id = "0",
    designation = "Member",
    name = "Username",
    img = "./img/dp.jpg",
    facebook = "#",
    twitter = "#",
    instagram = "#";
</script>

<style>
  * {
    text-align: center;
    color: #313131;
  }

  main {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .card {
    padding: 0 10px;
    display: flex;
    flex-direction: column;
    width: 310px;
    height: 490px;
    background: #ffffff;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
    border-radius: 20px;
    position: relative;
    overflow: hidden;
  }

  .dp {
    height: 214px;
    display: grid;
    justify-content: center;
    align-items: center;
  }

  .dp img {
    width: 170px;
    height: 170px;
    border-radius: 50%;
  }

  .badge {
    margin: 0 auto;
    padding: 0 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: fit-content;
    min-width: 150px;
    height: 30px;
    background: #d63e75;
    border-radius: 50px;
    color: #ffffff;
    font-size: 18px;
    font-family: "Roboto", sans-serif;
  }
  .title {
    width: 100%;
    height: 60px;
    font-family: Raleway;
    font-size: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #000000;
    margin-bottom: 25px;
  }

  .social {
    padding: 0 10px 20px;
    display: grid;
    grid-template-columns: repeat(3, 60px);
    grid-row-gap: 10px;
    justify-content: space-around;
  }

  .social img {
    width: 60px;
  }

  /* .detail {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 60px;
    font-size: 20px;
    position: absolute;
    bottom: 0;
    background-color: #c4c4c4;
  } */
</style>

<main>
  <div class="card">
    <div class="dp">
      <img src={`./img/members/${img}`} alt={name} />
    </div>
    <div class="badge">{designation}</div>
    <div class="title">{name}</div>
    <div class="social">
      <a href={facebook}>
        <img src="./img/svg/facebook.svg" alt="facebook" />
      </a>
      <a href={twitter}>
        <img src="./img/svg/twitter.svg" alt="twitter" />
      </a>
      <a href={instagram}>
        <img src="./img/svg/instagram.svg" alt="instagram" />
      </a>
    </div>
  </div>
</main>
