<script>
  import { onMount } from "svelte";

  export let id = "0",
    designation = "Member",
    name = "Username",
    img = "./img/dp.jpg",
    facebook = "#",
    twitter = "#",
    instagram = "#";
</script>

<style>
  * {
    text-align: center;
    color: #313131;
  }

  main {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .card {
    padding: 0 10px;
    display: flex;
    flex-direction: column;
    width: 155px;
    height: 245px;
    background: #ffffff;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
    border-radius: 8px;
    position: relative;
    overflow: hidden;
    z-index: 0;
  }

  .dp {
    height: 107px;
    display: grid;
    justify-content: center;
    align-items: center;
  }

  .dp img {
    width: 85px;
    height: 85px;
    border-radius: 50%;
    border: 2px solid #d63e75;
  }

  .badge {
    margin: 0 auto;
    padding: 5px 9px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: fit-content;
    min-width: 100px;
    background-color: #d63e75;
    border-radius: 50px;
    color: #ffffff;
    font-size: 11pt;
    font-family: "Roboto", sans-serif;
  }
  .title {
    width: 100%;
    height: 60px;
    font-family: Raleway;
    font-size: 12pt;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    text-align: center;
    margin: 10px 0 10px;
  }

  .social {
    padding: 0 10px 20px;
    display: grid;
    grid-template-columns: repeat(3, 25px);
    grid-template-rows: 25px;
    justify-content: space-around;
  }
  .social img {
    width: 25px;
  }
  .card-bg {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    background-size: cover;
    opacity: 0.2;
    z-index: -1;
  }
</style>

<main {id} on:click>
  <div class="card">
    <img class="card-bg" src={`./img/members/${img}`} alt="" />
    <div class="dp">
      <img src={`./img/members/${img}`} alt={name} />
    </div>
    <div class="badge">{designation}</div>
    <div class="title">{name}</div>
    <div class="social">
      <a href={facebook}>
        <img src="./img/svg/facebook.svg" alt="facebook" />
      </a>
      <a href={twitter}>
        <img src="./img/svg/twitter.svg" alt="twitter" />
      </a>
      <a href={instagram}>
        <img src="./img/svg/instagram.svg" alt="instagram" />
      </a>
    </div>
  </div>
</main>
