<script>
  import { slide, fade, scale } from "svelte/transition";
  import SmallCard from "./components/SmallCard.svelte";
  import Card from "./components/Card.svelte";
  import members from "./members.js";

  let details = false;
  let card = {};

  function showDetail(e) {
    card = members[e.currentTarget.id - 1];
    details = true;
  }
  function closeOverlay(e) {
    // console.log(e.target.id);
    details = e.target.id == "close" ? false : true;
  }
</script>

<style>
  .container {
    margin: auto;
    margin-bottom: 30px;
    width: fit-content;
    padding: 10px;
  }
  .title {
    text-align: center;
    margin-bottom: 30px;
  }
  .title h1 {
    font-family: "Roboto Condensed", sans-serif;
    font-size: 3em;
    color: #d63e75;
    margin-bottom: 10px;
  }
  .title h2 {
    margin: auto;
    padding: 0 20px 5px;
    border-radius: 50px;
    width: fit-content;
    background-color: #d63e75;
    color: #ffffff;
    font-weight: 400;
  }
  .top {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 10px;
    margin-bottom: 20px;
  }
  main {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 15px;
    grid-row-gap: 15px;
  }

  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #00000080;
  }

  @media only screen and (max-width: 960px) {
    main {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-column-gap: 10px;
      grid-row-gap: 15px;
    }
  }
  @media only screen and (max-width: 768px) {
    main {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-column-gap: 10px;
      grid-row-gap: 15px;
    }
  }
  @media only screen and (max-width: 580px) {
    main {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-column-gap: 10px;
      grid-row-gap: 15px;
    }
  }
</style>

<div class="container">
  <div class="title">
    <h1>Rotaract Club of Hailakandi</h1>
    <h2>Board of Directors 2019-20</h2>
  </div>
  <div class="top">
    <SmallCard
      on:click={showDetail}
      id={members[0].id}
      img={members[0].i}
      name={members[0].n}
      designation={members[0].d}
      facebook={members[0].s1}
      twitter={members[0].s2}
      instagram={members[0].s3} />
    <SmallCard
      on:click={showDetail}
      id={members[1].id}
      img={members[1].i}
      name={members[1].n}
      designation={members[1].d}
      facebook={members[1].s1}
      twitter={members[1].s2}
      instagram={members[1].s3} />
  </div>

  <main>
    {#each members as member (member.id)}
      {#if member.id !== 1 && member.id !== 2}
        <SmallCard
          on:click={showDetail}
          id={member.id}
          img={member.i}
          name={member.n}
          designation={member.d}
          facebook={member.s1}
          twitter={member.s2}
          instagram={member.s3} />
      {/if}
    {/each}
  </main>
</div>
{#if details}
  <div transition:fade id="close" class="overlay" on:click={closeOverlay}>
    <div transition:scale>
      <Card
        img={card.i}
        name={card.n}
        designation={card.d}
        facebook={card.s1}
        twitter={card.s2}
        instagram={card.s3} />
    </div>
  </div>
{/if}
